<template>
    <div class="buy-ticket-container">
        <div class="ordertype-container">
            <label class="label-order-type">Order type</label>

            <v-select
                    :items="[{label:'Market', id: 1, filter: 'market'},{ label: 'Order', id: 2, filter: 'order'}]"
                    v-model="selectedFilter"
                    single-line
                    item-text="label"
                    item-value="filter"
                    class="buyticket-filter"
            ></v-select>
        </div>

        <v-layout v-if="selectedFilter == 'market'">
            <BuyTicketNow></BuyTicketNow>
        </v-layout>
        <v-layout v-if="selectedFilter == 'order'">
            <BuyTicketOrder></BuyTicketOrder>
        </v-layout>
    </div>
</template>

<script>
    import BuyTicketNow from "./BuyTicketNow";
    import BuyTicketOrder from "./BuyTicketOrder";

    export default {
        name: "BuyTicket",
        components: {BuyTicketNow, BuyTicketOrder},
        data () {
            return {
                selectedFilter: 'market'
            }
        }
    }
</script>

<style scoped>
    .label-order-type {
        display: inline-block;
        width: 40%;
    }
    .buyticket-filter {
        display: inline-block;
        width: 60%;
    }
</style>