<template>
    <div class="sellticket-order-container">
        <v-card class="bid_card" xs12>
            <v-card-text class="bid_row" :style="{ paddingTop: 15, paddingBottom: 15}">
                <div class="maxunits-container" xs12>
                    <integer-plusminus :min="0"
                                       :max="maxUnits"
                                       :step="1"
                                       v-model="unitsCount">
                        <p>UNITS</p>
                        {{ unitsCount }}
                    </integer-plusminus>
                </div>
                <div class="sellticket-summary-row">
                    <label class="card_label_regular">RETURN</label>
                    <label class="card_label_bold">{{ formatPrice(subTotal) }}</label>
                </div>
                <div class="sellticket-summary-row">
                    <label class="card_label_regular">COMMISSION</label>
                    <label class="card_label_bold">{{ formatPrice(comission) }}</label>
                </div>
                <div class="sellticket-summary-row">
                    <label class="card_label_regular">STORAGE</label>
                    <label class="card_label_bold "> {{warehouseName}}</label>
                </div>
                <div class="sellticket-summary-row">
                    <label class="card_label_regular">STORAGE FEE</label>
                    <label class="card_label_bold "> {{currencySign}}- </label>
                </div>
                <div class="sellticket-summary-row">
                    <label class="card_label_regular">TOTAL RETURN</label>
                    <label class="card_label_bold">{{ formatPrice(subTotal) }}</label>
                </div>
            </v-card-text>
        </v-card>
        <v-btn block :class="unitsCount !== 0 ? 'sell-btn primary-button' : 'sell-btn primary-button-dis'"
               :btnDisabledBg="{color: '#686b70'}"
               :disabled="unitsCount === 0"
               v-on:click="createOrder"
        >
            SELL NOW
        </v-btn>
    </div>
</template>

<script>
    import { mapState, mapGetters } from 'vuex'
    import { AOrders } from '../store/actions';
    import { GOrders, GUser, GWines } from '../store/getters';
    import { IntegerPlusminus } from 'vue-integer-plusminus';

    export default {
        name: "SellTicketNow",
        data () {
            return {
                currencySign: '€',
                unitsCount: 0,
                maxUnitsCount: 3,
                modalVisible: false,
                isErrors: false,
                matchIds: new Set(),
                uniqueCaseIds: [],
            }
        },
        components: { IntegerPlusminus },
        computed: {
            ...mapGetters({
                domInfo: GWines.GET_WINE_DOM,
                wineDetails: GWines.GET_SELECTED_WINE_INFO,
                getWinePortfolio: GWines.GET_WINE_PORTFOLIO,
                maxBuyCommission: GUser.GET_USER_MAX_BUY_COMMISSION,
                minBuyCommission: GUser.GET_USER_MIN_BUY_COMMISSION,
                minSellCommission: GUser.GET_USER_MIN_SELL_COMMISSION,
                maxSellCommission: GUser.GET_USER_MAX_SELL_COMMISSION,
                buyCommission: GUser.GET_USER_BUY_COMMISSION,
                sellCommision: GUser.GET_USER_SELL_COMMISSION,
                caseSizeId: GWines.GET_SELECTED_WINE_CASE,
                warehouseName: GWines.GET_WAREHOUSE_NAME
            }),
            comission() {
                if (this.subTotal === 0) return 0
                let _commission = 0

                let _markup = (this.subTotal / 100) * this.markup

                let min = this.minBuyCommission;
                let max = this.maxBuyCommission;

                console.log(`min commission is ${min}`)
                console.log(`max commission is ${max}`)

                _commission = (this.subTotal / 100) * this.sellCommision

                if (_commission > max) _commission = max
                if (_commission < min) _commission = min

                return _commission.toFixed(2)
            },
            profit() {
                let totalPortfolio = 0
                let profit = 0
                let count = 0
                console.log(`profit ${profit}`)
                console.log(`totalPortfolio ${totalPortfolio}`)
                console.log(`count ${count}`)
                this.getWinePortfolio.forEach(element => {
                    if (element.profit === 0) {
                        let pricePerCase = element.currentValue / element.quantity
                        console.log(`in portfolio we have ${element.quantity} items with price ${element.currentValue}`)
                        for(let i = 0; i < element.quantity; i++) {
                            if(count < this.unitsCount) {
                                totalPortfolio = totalPortfolio + pricePerCase
                                count++
                                console.log(`profit ${profit}`)
                                console.log(`totalPortfolio ${totalPortfolio}`)
                                console.log(`count ${count}`)
                            }
                        }
                    } else {
                        let pricePerCase = element.currentValue - element.profit / element.quantity
                        console.log(`in portfolio we have ${element.quantity} items with price ${element.currentValue}`)
                        console.log(element)
                        for(let i = 0; i < element.quantity; i++) {
                            if(count < this.unitsCount) {
                                totalPortfolio = totalPortfolio + pricePerCase
                                count++
                                console.log(`profit ${profit}`)
                                console.log(`totalPortfolio ${totalPortfolio}`)
                                console.log(`count ${count}`)
                            }
                        }
                    }
                });

                console.log(`profit ${profit}`)
                console.log(`totalPortfolio ${totalPortfolio}`)
                console.log(`count ${count}`)
                return this.subTotal - totalPortfolio
            },
            total() {
                let _total = 0

                _total = parseFloat(this.subTotal) - parseFloat(this.comission)

                return _total
            },
            pricesPerUnit() {
                let _prices = []

                if (this.domInfo.length < this.unitsCount) return []
                for(let step = 0; step < this.unitsCount; step++){
                    _prices.push(this.domInfo[step].sellPrice)
                }

                return _prices
            },
            maxUnits() {
                let _subTotal = 0;
                let _subTotalTwo = 0
                let inPortfolio = this.calculateAvaiableUniqueCasesIds().length
                let inDom = this.domInfo.reduce(
                    (ac, value) => ac + value.sellQuantity,
                    _subTotalTwo
                )
                if(inDom <= inPortfolio){
                    return inDom
                } return inPortfolio
            },
            subTotal() {
                let sum = 0
                let countI = 0
                let offers = this.domInfo
                console.log(this.domInfo)

                for (let i = 0; i <= Object.keys(offers).length; i++) {
                    let offer = offers[i]
                    if (!offer) break
                    for (let j = 1; j <= offer.sellQuantity; j++) {
                        if (countI >= this.unitsCount || !offer.sellQuantity) break
                        countI++
                        sum += offer.sellPrice
                    }
                }
                return sum
            }
        },
        mounted () {
        },
        methods: {
            calculateAvaiableUniqueCasesIds() {
                let uniqueCasesIds = []
                this.getWinePortfolio.forEach(portfolioRecord => {
                    portfolioRecord.portfolioCases.forEach(uniqueCase => {
                        if(uniqueCase.status === 1) {
                            uniqueCasesIds.push(uniqueCase.uniqueCaseId)
                        }
                    })
                })
                return uniqueCasesIds
            },
            getMatchIds()  {
                let _matchIds = []
                let _counter = 0

                /*
                if (this.actionType === 'Buy') {
                  for (let step = 0; step < this.unitsCount; step++) {
                    if(this.domInfo[step].buyOrderId) {
                      _matchIds.push(this.domInfo[step].buyOrderId)
                    }
                  }
                } else {
                  for (let step = 0; step < this.unitsCount; step++) {
                    if(this.domInfo[step].sellOrderId) {
                      _matchIds.push(this.domInfo[step].sellOrderId)
                    }
                  }
                } */

                console.log('match id')
                console.log(_matchIds)

                return this.domInfo[0].sellOrderId
            },
            setModalVisible (visible) {
                this.modalVisible = visible;
            },
            createOrder() {
                let uniques = this.calculateAvaiableUniqueCasesIds();

                let _matchIds = []

                this.uniqueCaseIds = []
                this.matchIds.clear()

                if(this.unitsCount > this.maxUnits) {
                    Alert.alert('Not enough units in the portfolio', `There are only ${this.maxUnits} units`)
                    return
                }

                this.matchIds.add(this.domInfo[0].sellOrderId);
                for(let x = 0; x < this.unitsCount; x++) {
                    this.uniqueCaseIds.push(uniques[x])
                }

                let _payload = {
                    type: 2,
                    pricePerCase: this.domInfo[0].sellPrice,
                    isBuy: 0,
                    barCodes: null,
                    uniqueCasesIds: this.uniqueCaseIds,
                    wineId: this.wineDetails.wine.wineId,
                    matchIds: [...this.matchIds],
                    caseQuantity: this.unitsCount,
                    commission: this.comission,
                    total: this.subTotal,
                    caseSizeId: this.caseSizeId,
                    actionType: 'Sell',
                    warehouseName: this.warehouseName
                }

                console.log(_payload)

                this.$store.dispatch(AOrders.SET_CURRENT_ORDER, _payload);
                this.navigation.navigate('OrderConfirmation');
            }
        }
    }
</script>

<style scoped>
    .sellticket-order-container {
        width: 100%;
    }
    .maxunits-container {
        margin-bottom: 15px;
    }
    .v-card__text {
        padding-right: 15px;
    }
    .card_label_bold {
        color: #000;
        font-size: 14px;
        width: 40%;
        display: inline-block;
        text-align: right;
    }
    .card_label_regular {
        color: #9B9B9B;
        font-size: 14px;
        display: inline-block;
        width: 60%;
    }
    .sell-btn {
        margin-top: 15px;
    }
</style>