<template>
    <div class="sellticket-order-container">
        <wineInfo />

        <v-card class="bid_card" xs12>
            <v-card-text class="bid_row" :style="{ paddingTop: 15, paddingBottom: 15}">
                <div class="maxunits-container" xs12>
                    <integer-plusminus :min="1"
                                       :max="maxUnits"
                                       :step="1"
                                       v-model="unitsCount">
                        <p>UNITS</p>
                        {{ unitsCount }}
                    </integer-plusminus>
                </div>
                <div class="price-container" xs12>
                    <integer-plusminus :min="1"
                                       :max="1000000"
                                       :step="10"
                                       v-model="price">
                        <p>PRICE</p>
                        {{ price }}
                    </integer-plusminus>
                </div>

                <div class="sellticket-summary-row">
                    <label class="card_label_regular">COMMISSION</label>
                    <label class="card_label_bold">{{ formatPrice(commission) }}</label>
                </div>
                <div class="sellticket-summary-row">
                    <label class="card_label_regular">STORAGE</label>
                    <label class="card_label_bold ">{{warehouseName}}</label>
                </div>
                <div class="sellticket-summary-row">
                    <label class="card_label_regular">TOTAL RETURN</label>
                    <label class="card_label_bold">{{ formatPrice(subTotal) }}</label>
                </div>
            </v-card-text>
        </v-card>
        <v-btn block :class="unitsCount !== 0 ? 'sell-btn primary-button' : 'sell-btn primary-button-dis'"
               :btnDisabledBg="{color: '#686b70'}"
               :disabled="unitsCount === 0"
               v-on:click="createOffer"
        >
            CREATE OFFER
        </v-btn>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    import { AOrders } from '../store/actions';
    import { GUser, GWines} from '../store/getters';
    import { IntegerPlusminus } from 'vue-integer-plusminus';

    import wineInfo from './wineInfo';

    export default {
        name: "SellTicketOrder",
        components: { wineInfo, IntegerPlusminus },
        data () {
            return {
                currencySign: '€',
                unitsCount: 0,
                modalVisible: false,
                price: 100,
                isErrors: false,
                isModalVisible: false
            }
        },
        computed: {
            ...mapGetters({
                wine: GWines.GET_SELECTED_WINE_INFO,
                winePortfolio : GWines.GET_WINE_PORTFOLIO,

                maxBuyCommission: GUser.GET_USER_MAX_BUY_COMMISSION,
                minBuyCommission: GUser.GET_USER_MIN_BUY_COMMISSION,

                minSellCommission: GUser.GET_USER_MIN_SELL_COMMISSION,
                maxSellCommission: GUser.GET_USER_MAX_SELL_COMMISSION,

                buyCommission: GUser.GET_USER_BUY_COMMISSION,
                sellCommision: GUser.GET_USER_SELL_COMMISSION,
                markup:  GUser.GET_USER_MARKUP_COMMISSION,
                maxMarkup: GUser.GET_USER_MAX_MARKUP_COMMISSION,
                minMarkup: GUser.GET_USER_MIN_MARKUP_COMMISSION,
                caseSizeId: GWines.GET_SELECTED_WINE_CASE,
                warehouseName: GWines.GET_WAREHOUSE_NAME
            }),
            subTotal() {
                if (this.price * this.unitsCount > 0) {
                    let _commission = 0,
                        min = this.minSellCommission,
                        max = this.maxSellCommission;

                    _commission = ((this.price * this.unitsCount) / 100) * this.sellCommision;

                    if (_commission > max) _commission = max;
                    if (_commission < min) _commission = min;

                    return this.price * this.unitsCount - _commission
                } else {
                    return 0;
                }
            },
            markupComission() {
                if (this.unitsCount === 0) return 0
                let _total = 0

                _total = _total + (( this.price / 100) * this.markup)


                if (_total > this.maxMarkup) _total = this.maxMarkup
                if (_total < this.minMarkup) _total = this.minMarkup

                console.log(`markup commission is ${_total}`)

                return _total
            },
            total() {
                let _total = 0

                _total = this.subTotal - this.commission
                let count = 0
                console.log(`subTotal ${ this.subTotal }`)
                console.log(`commission ${this.commission}`)
                console.log(`total ${ _total }`)

                this.winePortfolio.forEach(element => {
                    if (element.profit === 0) {
                        let pricePerCase = element.currentValue / element.quantity
                        console.log(`pricePerCase ${pricePerCase}`)
                        let dif = pricePerCase - this.price
                        console.log(`total ${_total}`)
                        console.log(`dif ${dif}`)
                        for(let i = 0; i < element.quantity; i++) {
                            console.log('roolsadd')
                            if(count < this.unitsCount) {
                                _total = _total - pricePerCase;
                                count++
                                console.log(`after total ${_total}`)
                            }
                        }
                    } else {
                        let pricePerCase = element.currentValue + element.profit / element.quantity
                        console.log(`pricePerCase ${pricePerCase}`)
                        let dif = pricePerCase - this.price
                        console.log(`total ${_total}`)
                        console.log(`dif ${dif}`)
                        for(let i = 0; i < element.quantity; i++) {
                            console.log('roolsadd')
                            if(count < this.unitsCount) {
                                _total = _total - pricePerCase;
                                count++
                                console.log(`after total ${_total}`)
                            }
                        }

                    }
                    _total = this.subTotal
                });

                console.log(`total ${_total}`)

                return _total
            },
            commission() {
                let _commission = 0
                if (this.price * this.unitsCount === 0) return 0;

                let min = this.minSellCommission;
                let max = this.maxSellCommission;

                _commission = ((this.price * this.unitsCount) / 100) * this.sellCommision

                if (_commission > max) _commission = max;
                if (_commission < min) _commission = min;

                return _commission.toFixed(2);
            },
            maxUnits() {
                let uniqueCasesIds = [];
                this.winePortfolio.forEach(portfolioRecord => {
                    portfolioRecord.portfolioCases.forEach(uniqueCase => {
                        console.log('rec')
                        console.log(uniqueCase)
                        if(uniqueCase.status === 1) {
                            uniqueCasesIds.push(uniqueCase.uniqueCaseId)
                            console.log(uniqueCase)
                        }
                    })
                });
                console.log('max units -> ' + uniqueCasesIds.length)
                return uniqueCasesIds.length;
            }
        },
        methods: {
            calculateAvaiableUniqueCasesIds() {
                let uniqueCasesIds = []
                this.winePortfolio.forEach(portfolioRecord => {
                    portfolioRecord.portfolioCases.forEach(uniqueCase => {
                        if(uniqueCase.status === 1) {
                            uniqueCasesIds.push(uniqueCase.uniqueCaseId)
                        }
                    })
                })
                return uniqueCasesIds
            },
            setModalVisible (visible) {
                this.modalVisible = visible;
            },
            createOffer () {
                let uniq = this.calculateAvaiableUniqueCasesIds();
                console.log('avaiable uniques');
                console.log(uniq);

                const getUniqueCasesIds = () => {
                    let _uniqueCasesIds = [];
                    if (this.winePortfolio.length) {
                        let portfolio = this.winePortfolio[0];
                        for (let k = 0; k <= this.unitsCount - 1; k++) {
                            _uniqueCasesIds.push(portfolio.uniqueCaseIds[k])
                        }
                    }

                    return _uniqueCasesIds
                };

                let _payload = {
                    type: 1,
                    caseSizeId: this.caseSizeId,
                    pricePerCase: this.price,
                    isBuy: 0,
                    barCodes: null,
                    uniqueCasesIds: getUniqueCasesIds(),
                    wineId: this.wine.wine.wineId,
                    matchIds: null,
                    caseQuantity: this.unitsCount,
                    commission: this.commission,
                    total: this.subTotal,
                    actionType: 'Offer',
                    warehouseName: this.warehouseName
                };

                this.$store.dispatch(AOrders.SET_CURRENT_ORDER, _payload);
                this.$router.push({ path: '/order-confirmation/' });
            },
            confirmOrder() {
                const getUniqueCasesIds = () => {
                    let _uniqueCasesIds = []
                    if (this.winePortfolio.length) {
                        let portfolio = this.winePortfolio[0]
                        for (let k = 0; k <= this.unitsCount - 1; k++) {
                            _uniqueCasesIds.push(portfolio.uniqueCaseIds[k])
                        }
                    }

                    return _uniqueCasesIds
                }

                let _payload = {
                    caseSizeId: this.caseSizeId,
                    type: 1,
                    pricePerCase: this.price,
                    isBuy: 0,
                    barCodes: null,
                    uniqueCasesIds: getUniqueCasesIds(),
                    wineId: this.wine.wine.wineId,
                    matchIds: null,
                    caseQuantity: this.unitsCount
                };

                this.$store.dispatch(AOrders.CREATE_ORDER, _payload).then((res) => {
                    console.log(res)
                }).catch((err) => {
                    if(err.data.errors.length > 0) {
                        console.log(this.errorText = err.data.errors[0].Message);
                    } else if (err.data.valiationErrors.length > 0) {
                        console.log(err.data.valiationErrors[0].exception.Message);
                    }
                })
            }
        }
    }
</script>

<style scoped>
    .sellticket-order-container {
        width: 100%;
    }
    .v-card__text {
        padding-right: 15px;
    }
    .card_label_thead {
        color: #9B9B9B;
        font-size: 12px;
    }
    .card_label_bold {
        color: #000;
        font-size: 14px;
        width: 40%;
        display: inline-block;
        text-align: right;
    }
    .card_label_regular {
        color: #9B9B9B;
        font-size: 14px;
        display: inline-block;
        width: 60%;
    }
    .order_type {
        font-size: 15px;
        color: #014E7A;
        font-weight: bold;
    }
    .wine_details_name {
        color: #000;
        font-size: 30px;
    }
    .wine_details_second_column {
        flex: 1;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }
    .wine_details_desc {
        color:  #9B9B9B;
        font-size: 15px;
        padding-top: 7px;
        text-align: left;
    }
    .wine_details_sell {
        padding-left: 35px;
        padding-top: 30px;
    }
    .info {
        margin-top: 40px;
    }
    .info_thead {
        padding-left: 35px;
        padding-bottom: 8px;
        border-bottom-width: 1px;
        border-bottom-color: #EEECEC;
    }
    .info_row {
        padding-left: 35px;
        height: 35px;
        align-items: center;
        justify-content: center;
    }
    .bid_card {
        margin-top: 15px;
        background-color: #fff;
        border-radius: 10px;
        border-width: 0;
        height: 300px;
    }
    .bid_header {
        height: 15px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
    }
    .bid_row {
        height: 65px;
        justify-content: center;
        align-items: center;
        padding-left: 15px;
        padding-right: 25px;
        border-top-width: 1px;
        border-top-color: #EEECEC;
    }
    .bid_footer {
        height: 65px;
        justify-content: center;
        align-items: center;
        padding-left: 20px;
        padding-right: 35px;
        border-top-width: 1px;
        border-top-color: #EEECEC;
    }
    .bid_footer_buy {
        margin-top: 20px;
        height: 65px;
        justify-content: center;
        align-items: center;
        padding-left: 20px;
        padding-right: 35px;
        border-top-width: 1px;
        border-top-color: #EEECEC;
    }
    .price_row {
        margin-top: 10px;
        margin-bottom: 15px;
    }
    .button_wrapper {
        flex-direction: row;
        flex: 1;
        align-items: center;
        justify-content: center;
    }

    .primary-button {
        margin-top: 10px;
        margin-bottom: 10px;
        border-radius: 25px;
        background-color: #B30000;
        width: 240px;
        height: 32px;
    }

    .primary-button-dis {
        margin-top: 10px;
        margin-bottom: 10px;
        border-radius: 25px;
        background-color: #9B9B9B;
        width: 240px;
        height: 32px;
    }

    .primary_button_label {
        color: #fff;
        font-size: 15px;
    }
    .card_item {
        border-width: 0;
        border-bottom-width: 0;
    }

    .active {
        background-color: #B30000;
        height: 52px;
    }
    .maxunits-container,
    .price-container {
        margin-bottom: 15px;
    }
    .sell-btn {
        margin-top: 15px;
    }
</style>