<template>
    <div class="dominfo-container">
        <v-layout>
            <v-flex class="info" v-if="isDomInfoAvaiable">
                <v-row class="info_thead">
                    <v-col :size="1">
                        <v-label class="card_label_thead">BEST OFFERS</v-label>
                    </v-col>
                    <v-col :size="1">
                        <v-label class="card_label_thead">UNITS</v-label>
                    </v-col>
                    <v-col :size="1">
                        <v-label class="card_label_thead">STORAGE</v-label>
                    </v-col>
                </v-row>


                <v-row class="info_row" v-for='item in domInfo'>
                    <v-col :size="1">
                        <v-label class="card_label_bold">{{ formatPrice(zero(item.buyPrice)) }}</v-label>
                    </v-col>
                    <v-col :size="1">
                        <v-label class="card_label_bold">{{ formatPrice(zero(item.buyQuantity)) }}</v-label>
                    </v-col>
                    <v-col :size="1">
                        <v-label class="card_label_bold">{{ item.warehouseName }}</v-label>
                    </v-col>
                </v-row>
            </v-flex>
            <v-layout v-else :style="{ flex: 1, justifyContent: 'center',
        alignItems: 'center', marginTop: 15 }">
                <v-label>There are no offers</v-label>
            </v-layout>
        </v-layout>
    </div>
</template>

<script>
    import { mapState, mapGetters } from 'vuex'
    import { AOrders } from '../store/actions';
    import { GOrders, GCommon, GWines} from '../store/getters';

    export default {
        name: "DomInfo",
        data() {
            return {
                isDomInfoAvaiable: false
            }
        },
        watch: {
            domInfo: {
                immediate: true,
                deep: true,
                handler (v) {
                    console.log('updated!')
                    console.log(this.actionType)
                    console.log(this.domInfo)

                    let orders = this.domInfo.filter(item => {
                        if (this.actionType === 'Buy' || this.actionType === 'Bid') {
                            return item.buyPrice > 0
                        } else {
                            return item.sellPrice > 0
                        }
                    })

                    console.log(orders)

                    this.isDomInfoAvaiable = orders.length > 0
                }
            }
        },
        computed: {
            ...mapGetters({
                domInfo: GWines.GET_WINE_DOM,
                currencySign: GCommon.GET_CURRENCY_SIGN
            })
        },
        methods: {
            zero(item) {
                return item === 0 ? '-' : item
            }
        }
    }
</script>

<style scoped>
    .dominfo-container {
        margin-bottom: 30px;
    }

    .info {
        margin-top: 40px;
    }

    .info_thead {
        padding-bottom: 8px;
        padding-left: 30px;
        padding-right: 20px;
        border-bottom-width: 1px;
        border-bottom-color: #EEECEC;
    }
    .info_row {
        height: 35px;
        padding-left: 30px;
        padding-right: 20px;
        align-items: center;
        justify-content: center;
    }
    .card_label_thead {
        color: #9B9B9B;
        font-size: 12px;
        font-family: Montserrat_medium;
    }
    .card_label_bold {
        color: #000;
        font-size: 15px;
        font-family: Montserrat_medium;
    }
    .card_label_regular {
        color: #9B9B9B;
        font-size: 15px;
        font-family: Montserrat_medium;
    }
</style>